import React, { useRef } from 'react';

const ImageToggle = ({ primaryTag, secondaryTag }) => {

    const imageRef = useRef(null);

    return (
        <img
            onMouseOver={() => {
                imageRef.current.src = secondaryTag;
            }}
            onMouseOut={() => {
                imageRef.current.src = primaryTag;
            }}
            src={primaryTag}
            alt=""
            ref={imageRef}
        />
    )
};

export default ImageToggle;
